<template>

    <div id="board">
        
        <h1>Iniciar Sesión</h1>
        <div id="msgLogin">{{msg_login}}</div>
        <form id="formLogin" @click="msgDisplay">

            <input type="email" name="email" id="email" placeholder="Correo electrónico">
            <input type="password" name="password" placeholder="Contraseña">

            <button type="button" class="btn-send btn btn-primary mt-4" @click="loginUser">Ingresar</button>
            <a id="bton-special" href="/register"><button type="button" class="btn-send btn btn-primary mb-4 mt-2">Crear Cuenta</button></a>

        </form>
        <a href="/forget-passwd"><h5>Olvidaste tu contraseña</h5></a> 
    </div>

</template>

<script>
import axios from 'axios'

export default {
    name: 'MyLogin',
    data(){
        return {
            msg_login: "",
            credenciales: false,
        }
    },
    mounted(){
        this.msgDisplay();
    },
    methods: {
        loginUser(){
            const form = document.getElementById("formLogin");
            const loginData = new FormData(form);
            
            axios
                .post("/api/login/login/",loginData,{
                    headers: {"Content-Type": "multipart/form-data",},
                })
                .then(response =>{
                    this.msg = response.data;
                
                    if(this.msg.Credenciales){
                        this.keepSession(response.data.id);
                        console.log(this.msg.Credenciales);
                        location.href = "/";
                    }else{
                        this.msg_login = "Error: Usuario o contraseña incorrecta";
                        document.getElementById("msgLogin").style.display="block";
                    }
                    
                })
                .catch(error => {
                    console.log(error)
                })
        },
        msgDisplay(){
            document.getElementById("msgLogin").style.display="none";
        },
        keepSession(id){
            localStorage.setItem("user_session",id);
        },
        deleteId(){
            localStorage.removeItem("user_session");
        },
        changeId(){
            localStorage.setItem("user_session",27);
        },
    },
}
</script>

<style scoped>

#board{
    height: 600px;
    text-align: center;
    background-color: #F2F5F9;
}

#msgLogin{
    background-color: #DBE2EF;
    width: 300px;
    padding: 0.6rem 1.2rem 0.5rem 1.2rem;
    margin: 20px auto;
    display: none;
}


h1{
    padding-top: 5rem;
    margin-bottom: 2rem;
    color: #2c3e50;
}

form{
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: center;
}

input,button{
    width: 300px;
    padding: 0.5rem 1rem 0.5rem 1rem;
}

input{
    width: 320px;
    margin-top: 10px;
}

button{
    margin-top: 0;
    width: 320px;
    color: white;
    background-color: #111729;
    cursor: pointer;
}


a{
    color: #2c3e50;
    text-decoration: none;
}

.btn-send{
    background-color: #03506F !important;
    border-color: #03506F !important;
}

.btn-send:hover{
    background-color: #0f87b7 !important;
    border-color: #0f87b7 !important;
}


</style>
