<script>
import axios from 'axios'

export default{
    name: 'MyFooter',
    methods:{
        endTour(){
            var me = localStorage.getItem("metricas");
            if(me!= null){
                console.log(JSON.parse(me));

                axios
                    .post("/api/save_metric",JSON.parse(me))
                    .then(response =>{
                        this.msg = response.data;
                        console.log("msg: ",this.msg);
                        console.log("status: ",this.msg.status);
                        console.log("Enviado metricas");
                    })
                    .catch(error => {
                        console.log(error)
                    })
                //borrando local storage
                localStorage.removeItem("metricas");
            }

            
        },
    }
}
</script>

