<script>
import axios from 'axios'

import { Carousel, Navigation, Slide, Pagination } from 'vue3-carousel';

import "vue3-carousel/dist/carousel.css";

export default{
    name: "MyHome",
    components: {
        Pagination,
        Carousel,
        Slide,
        Navigation,
    },
    data(){
        return{
            settings:{
                itemsToShow: 1,
                snapAlign: 'center',
            },
            breakpoints: {
                400:{
                    itemsToShow: 1,
                    snapAlign: 'center',
                },
                570:{
                    itemsToShow: 2,
                    snapAlign: 'center',
                },
                850: {
                    itemsToShow: 3,
                    snapAlign: 'center',
                },
                1100: {
                    itemsToShow: 4,
                    snapAlign: 'center',
                }
            },

            isFavorite: [],
            recentPolos: [],
            popularPolos: [],
            userSession: false,
        }
    },
    beforeMount(){
        this.checkUserSession()
        this.checkFavoritesStorage()
        this.getRecentProducts()
        this.getPopularProducts()
    },
    methods: {
        getRecentProducts() {
            axios
                .get("/api/home/recentPolos/")
                .then(response =>{
                    this.recentPolos = response.data
                    console.log("recen: ",this.recentPolos)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        getPopularProducts() {
            axios
                .get("/api/home/popularPolos/")
                .then(response =>{
                    this.popularPolos = response.data
                    console.log("pop: ",this.popularPolos)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        addFavorite(id){
            
            //console.log(id);
            //console.log(this.isFavorite)
            
            if(this.isFavorite.indexOf(id) > -1) this.isFavorite.splice(this.isFavorite.indexOf(id),1)
            else this.isFavorite.push(id);

            //linking product to user

            if(this.isFavorite.length == 0) localStorage.removeItem("list_favorites");
            else localStorage.setItem("list_favorites",this.isFavorite);

            const fav = {
                "id_polo": null,
                "id_user": null
            }

            fav["id_polo"] = id;
            fav["id_user"] = localStorage.getItem("user_session")

            axios
                .post("/api/registerPoloFavorite/",fav)
                .then(response =>{
                    this.msg = response.data;
                    console.log(this.msg);
                })
                .catch(error => {
                    console.log(error)
                })


        },
        changeIcon(value){
            if(value === false){
                return require('../assets/icons/heart.svg');
            }else{
                return require('../assets/icons/heart-fill.svg');
            }
        },
        checkUserSession(){
            const activeUserSession = localStorage.getItem("user_session");
            if(activeUserSession != null){
                this.userSession = true;
            }else{
                this.userSession = false;
            }

        },
        checkFavoritesStorage(){
            const checkFav = localStorage.getItem("list_favorites");
            const listFav = [];

            if(checkFav != null){
                const strFavList = checkFav.split(",");
                strFavList.forEach( id_fav => listFav.push(parseInt(id_fav)));
                listFav.forEach( item => this.isFavorite.push(item));
                //console.log(listFav);
            }
            
        },
        deleteStorage(){
            localStorage.removeItem("list_favorites");
        },

    },
}
</script>

