import { createWebHistory, createRouter } from "vue-router";
import MyHome from "@/views/MyHome.vue";
import MyLogin from "@/views/MyLogin.vue";

const routes = [
  {
    path: "/",
    name: "MyHome",
    component: MyHome,
  },
  {
    path: "/login",
    name: "MyLogin",
    component: MyLogin,
  },
  {
    path: "/register",
    name: "MyRegister",
    component: () => import('@/views/MyRegister.vue')
  },
  {
    path: "/try-on/:id",
    name: "MyVisualizer",
    component: () => import('@/views/MyVisualizer.vue')
  },

  {
    path: "/myitems",
    name: "MyPagItems",
    component: () => import('@/views/MyPagItems.vue')
  },

  {
    path: "/shopcar",
    name: "MyShopCar",
    component: () => import('@/views/MyShopCar.vue')
  },

  {
    path: "/categories",
    name: "MyCatalog",
    component: () => import('@/views/MyCatalog.vue')
  },

  {
    path: "/forget-passwd",
    name: "MyForgetPasswd",
    component: () => import('@/views/MyForgetPasswd.vue')
  },

  {
    path: "/passwd-recover/:id",
    name: "MyPasswdRecover",
    component: () => import('@/views/MyPasswdRecover.vue')
  },
  {
    path: "/items/:id",
    name: "items",
    component: () => import('@/views/MyShirt.vue')
  },
  {
    path: "/profile",
    name: "profile",
    component: () => import('@/views/MyProfile.vue')
  },

  {
    path: "/favorites",
    name: "MyFavorites",
    component: () => import('@/views/MyFavorites.vue')
  },

];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;